import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const FooterBlock = styled.div`
    background: #262626; color: #818181;
    .footer_utill {
        padding: 16px 0; text-align: center;
        border: 1px solid #494949;
        li {
            display: inline-block; font-size: 13px;
            margin: 3px 19px; font-weight: bold;
            &:first-child { color: #fff; }
        }
    }
    .footer_content {
        text-align: center;
        .row {
            position: relative;
            .footer_ccm {
                position: absolute;
                right:0; top:0;
                img { width: 170px; }
            }
            .footer_logo {
                margin-top: 41px; margin-bottom: 24px;
                height: 45px;
                display: flex;
                align-content: center;
                justify-content: center;
                img { 
                    flex: 0 1 210px;
                    height: 20px;
                    align-self: center;
                }
            }
            .info {
                width: 650px; margin: 0 auto 14px;
                font-size: 12px;
                span {
                    letter-spacing: -0.5px;
                    margin-right: 10px; padding-right: 10px;
                    position: relative;
                    &::after {
                        position: absolute; content:'';
                        width:1px; height: 10px;
                        right:0; top:2px; background: #818181;
                    }
                    &:nth-child(3)::after,
                    &:last-child::after { display: none;}
                }
            }
            .copy {
                font-size: 12px;
                padding-bottom: 35px;
            }
        }
    }
`;

const Footer = () => {
    return (
        <FooterBlock>
            <ul className="footer_utill">
                <li><Link to="/">개인정보처리방침</Link></li>
                <li><Link to="/">멤버스 이용약관</Link></li>
                <li><Link to="/">가맹 안내</Link></li>
                <li><Link to="/">대량쿠폰구매</Link></li>
                <li><Link to="/">채용안내</Link></li>
                <li><Link to="/">Sitemap</Link></li>
                <li><Link to="/">점주의 방</Link></li>
            </ul>
            <div className="footer_content">
                <div className="row">
                    <div className="footer_ccm">
                        <img src="/images/footer_ccm.png" alt=""></img>
                    </div>
                    <div className="footer_logo">
                        <img src="/images/bottom_logo.gif" alt=""></img>
                    </div>
                    <p className="info">
                        <span>서울특별시 강남구 논현로 636 이디야빌딩(서울특별시 강남구 논현동 221-17)</span>
                        <span>TEL : 02-543-6467</span>
                        <span>FAX : 02-543-7191</span>
                        <span>사업자등록번호 : 107-86-16302</span>
                        <span>통신판매업 신고 : 강남 제 002519호</span>
                        <span>대표이사 : 문창기</span>
                    </p>
                    <p className="copy">ⓒ 2017 EDIYA COFFEE COMPANY. ALL RIGHTS RESERVED..</p>
                    </div>
                </div>
        </FooterBlock>
    );
};

export default Footer;